:root {
    --inputHeigth: 3.125rem;
    --inputWidth: 37.6rem;
    --checkWidth: 3.75rem;
    --checkHeigth: 3.75rem;
    --randomColor: Math.floor(Math.random()*16777215).toString(16);
}

html {
    background-color: #e0edf5;
}

.container {
    
    display: -ms-flexbox;
    
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    margin: auto;
    width: 37.6rem;
    width: var(--inputWidth);
}

.container .layout-add-task {

        background-color: white;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -ms-flex-align: center;
            align-items: center;
        padding: 0 5px 0px 5px;
        height: 3.125rem;
        height: var(--inputHeigth);
        border-style: solid;
        border-color: #b1abab;
        border-width: 1px;
        border-radius: 5px;
        margin-bottom: 10px;
    }

.container .layout-add-task .text-field-input {
            width: 375px;
            border-style: none;
        }

.container .layout-add-task .btn-task {
            background-color: #3ecc9f;
            border-radius: inherit;
            border-width: 1px;
            border-style: groove;
            border-color: #3ecc9f;
            height: calc(3.125rem / 1.2);
            height: calc(var(--inputHeigth) / 1.2);        
            width: 3.75rem;        
            width: var(--checkWidth);
            margin-left: 10px;
        }

.container .loader {
        border: 5px solid #f3f3f3;
        border-radius: 150%;
        border-top: 6px solid #3498db;
        width: 20px;
        height: 20px;
        -webkit-animation: spin 2s linear infinite;
                animation: spin 2s linear infinite;

    }
    
@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
    
@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
} 


