.task-enter {
    opacity: 0.1;
    -webkit-transition: all 500ms ease-out;
    -o-transition: all 500ms ease-out;
    transition: all 500ms ease-out;  
  }
  
.task-enter-active {
    opacity: 0.5;
    -webkit-transition: all 500ms ease-out;
    -o-transition: all 500ms ease-out;
    transition: all 500ms ease-out;
}

.task-exit {
    opacity: 1;
    text-decoration: line-through;
}

.task-exit-active {
    opacity: 0.1;
    -webkit-transition: all 500ms ease-out;
    -o-transition: all 500ms ease-out;
    transition: all 500ms ease-out;
    text-decoration: line-through;
}

.container {

    max-height: 610px;
    overflow: auto;
}

.container .content {

        display: grid;
        grid-template-columns: var(--checkWidth) calc((var(--inputWidth) - 3.8rem));
        grid-template-columns: var(--checkWidth) calc((var(--inputWidth) - 3.8rem));
        grid-column-gap: 1px;
        -webkit-column-gap: 1px;
                column-gap: 1px;
        margin: auto;
        margin-bottom: 1px;  
        border-radius: 3px;
    }

.container .content .btn-checkbox {
            display: -ms-flexbox;
            display: flex;
            -ms-flex-align: center;
                align-items: center;
            -ms-flex-pack: center;
                justify-content: center;
            background-color: #f54d4d;
            width: calc(var(--checkWidth));
            width: calc(var(--checkWidth));
            height: var(--checkHeigth);
            height: var(--checkHeigth);
            border-radius: inherit;
        }

.container .content .added-task {
            display: -ms-flexbox;
            display: flex;
            -ms-flex-align: center;
                align-items: center;
            background-color: #51a068;
            width: calc((var(--inputWidth) - 3.8rem - 25px));
            width: calc((var(--inputWidth) - 3.8rem - 25px));
            height: var(--checkHeigth);
            height: var(--checkHeigth);
            border-radius: inherit;
            padding-left: 25px;
        }

.container .content .added-task:hover {
                color: white;
            }


